import React, { useState } from 'react';
import { GrLocation, GrMailOption } from 'react-icons/gr';
import { RiLinkedinBoxLine, RiInstagramLine } from 'react-icons/ri';

import firebase from '../../congif/Fire'
import '../contact/contact.css';

const Contact = () => {
        const [email, setEmail] = useState('');
        const [name, setName] = useState('');
        const [msg, setMsg] = useState('');
        
        function onSubmit(e){
            e.preventDefault();
            firebase
                .firestore()
                .collection('contacts')
                .add({
                    email,
                    name,
                    msg
                })
                .then(()=>{
                    setEmail('')
                    setName('')
                    setMsg('')
                })
        }

        return(
            <div>
                    <div className="card-contact">
                           <div className="contact-details">
                                <h2 className="social">Contact Me</h2>
                                <p className="info-contact"> <GrLocation size={25} /> - India, Maharashtra, Mumbai</p>
                                <p className="info-contact"> <GrMailOption size={25} /> - ksiddhant46@gmail.com</p>
                                <p className="social-icon">
                                     <a href='https://www.linkedin.com/in/siddhant-kapadne-459013159'><RiLinkedinBoxLine size={30} /></a>
                                     <a href='https://www.instagram.com/siddhant_kapadne/'><RiInstagramLine size={30} /></a>
                                </p>
                           </div>

                            <h1 className="or-mid">[ or ]</h1>

                            <form className="contact-form" onSubmit={onSubmit}>
                                <h2 className="hello-msg">Just drop a "Hello"</h2>
                                <input className="contact-input" placeholder="Email ID" required value={email} onChange={e => setEmail(e.currentTarget.value)} />
                                <input className="contact-input" placeholder="Name (Optional)"value={name} onChange={e => setName(e.currentTarget.value)} />
                                <textarea className="contact-input" placeholder="Message" required value={msg} onChange={e => setMsg(e.currentTarget.value)} />
                                <button className="contact-submit">Submit</button>
                            </form>
                    </div>
            </div>
        );
}
export default Contact;